import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../css/alienTyping.css";
import { HiOutlinePresentationChartLine } from "react-icons/hi";
import "../css/bitbybit.css";
import { useUser } from "./useUser";
import db from "../firebase";
import { useHistory } from "react-router-dom";

const BitbyBit = () => {
  const [viewDecp, setViewDecp] = useState(true);
  const [rules, setRules] = useState(false);
  const [roundOne, setRoundOne] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [userEmail, setUserEmail] = useState(null);
  let history = useHistory();

  let user = useUser(true);

  useEffect(() => {
    setUserEmail(user.email);
    userIsRegistered();
  }, [user, userIsRegistered]);
  function userIsRegistered() {
    db.collection("registerBitbyBit").onSnapshot((snapshot) => {
      let data = snapshot.docs.map((doc) => doc.data());
      if (data) {
        data.map((user) => {
          if (user.email.includes(userEmail)) {
            setIsRegistered(true);
            return true;
          } else return false;
        });
      }
    });
  }

  const handleRegister = () => {
    if (user) {
      let state = false;
      db.collection("registerBitbyBit").onSnapshot((snapshot) => {
        let data = snapshot.docs.map((doc) => doc.data());
        if (data) {
          data.map((user) => {
            if (user.email.includes(userEmail)) {
              state = true;
              setIsRegistered(true);
              return;
            }
          });
          if (!state) {
            history.push("/registerOnBitbyBit");
          }
        }
      });
    }
  };

  const handleDesp = (e) => {
    setRules(false);
    setViewDecp(true);
    setRoundOne(false);
  };
  const handleRules = () => {
    setRules(true);
    setViewDecp(false);
    setRoundOne(false);
  };
  const handleRoundOne = () => {
    setRoundOne(true);
    setRules(false);
    setViewDecp(false);
  };

  const styles = {
    visibleBorder: {
      borderBottom: "4px solid red",
    },
    hiddenBorder: {
      border: "none",
    },
  };
  return (
    <>
      <div className="alientyping_container bitbybit_container">
        <h1 className="bitbybit_heading">BitbyBit</h1>
        {viewDecp && (
          <>
            <h2 style={{ color: "#dcdde9" }}>Description</h2>
            <p
              style={{
                width: "900px",
                lineHeight: "1.5",
                textAlign: "justify",
              }}
            >
              A Bit By Bit is a presentation intended to present information
              technically about a particular topic. For Example, How does the
              future is going to evolove or how does the quantum comnputers
              works.
            </p>
          </>
        )}
        {rules && (
          <>
            <h2>Rules</h2>
            <div className="main_container">
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Any technical topics are free to choose</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>A team can consist maximum of three members</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Individual participants can also participate</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>There will be only one round</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Classroom with projector will be provided</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Decision of the Panel members will be final</p>
              </div>
            </div>
          </>
        )}

        {roundOne && (
          <>
            <h2>RoundOne</h2>
            <div className="main_container">
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Presentation should have 8 to 10 slides</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Each team carry 6  8 minutes for their presentation</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>No Negative Marking</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Use professional language </p>
              </div>
            </div>
          </>
        )}

        <div className="alien_buttonGrps">
          <button
            id="description"
            onClick={() => handleDesp()}
            style={viewDecp ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Description
          </button>
          <button
            onClick={() => handleRules()}
            style={rules ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Rules
          </button>
          <button
            onClick={() => handleRoundOne()}
            style={roundOne ? styles.visibleBorder : styles.hiddenBorder}
          >
            Round One
          </button>

          <Link to="/leaderboard_bitbybit" target="_blank">
            <button>View Scoreboard</button>
          </Link>
        </div>
        {isRegistered && (
          <>
            <h4>
              Whatsapp link:
              <span>
                <a
                  target="_blank"
                  href="https://chat.whatsapp.com/GzG3JZQS1oULt7xBk9jBu6"
                  style={{ textDecoration: "underline", color: "white" }}
                >
                  &nbsp;https://chat.whatsapp.com/GzG3JZQS1oULt7xBk9jBu6
                </a>
              </span>
            </h4>
            <button> Registered</button>
          </>
        )}
        {isRegistered || (
          <button onClick={handleRegister} style={{ cursor: "pointer" }}>
            Register
          </button>
        )}
      </div>
    </>
  );
};

export default BitbyBit;
