import React from "react";
import App from "./App";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import AlienTyping from "./components/AlienTyping";
import BitbyBit from "./components/BitbyBit";
import LogoLooposy from "./components/LogoLoopsy";
import TechieWeb from "./components/TechieWeb";
import EventAdmin from "./components/EventAdmin";
import AlterAlienTyping from "./components/AlterAlienTyping";
import AlterBitbyBit from "./components/AlterBitbyBit";
import AlterLogoLoopsy from "./components/AlterLogoLoopsy";
