import RegisterTyping from "./components/RegisterTyping";
import RegisterBitbyBit from "./components/RegisterBitbyBit";
import RegisterQuiz from "./components/RegisterQuiz";
import RegisterLogoLoopsy from "./components/RegisterLogoLoopsy";
