import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "../css/alienTyping.css";
import { BsKeyboard } from "react-icons/bs";
import { useUser } from "./useUser";
import { useHistory } from "react-router-dom";
import db from "../firebase";
import { useRegister } from "./useRegister";

const AlienTyping = () => {
  const [viewDecp, setViewDecp] = useState(true);
  const [rules, setRules] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [userEmail, setUserEmail] = useState(false);
  let history = useHistory();
  let user = useUser(true);

  useEffect(() => {
    setUserEmail(user.email);
    userIsRegistered();
  }, [user, userIsRegistered]);
  function userIsRegistered() {
    db.collection("registerTyping").onSnapshot((snapshot) => {
      let data = snapshot.docs.map((doc) => doc.data());
      if (data) {
        data.map((user) => {
          if (user.email.includes(userEmail)) {
            setIsRegistered(true);
            return true;
          } else return false;
        });
      }
    });
  }

  const handleRegister = () => {
    if (user) {
      let state = false;
      db.collection("registerTyping").onSnapshot((snapshot) => {
        let data = snapshot.docs.map((doc) => doc.data());
        if (data) {
          data.map((user) => {
            if (user.email.includes(userEmail)) {
              state = true;
              setIsRegistered(true);
              return;
            }
          });
          if (!state) {
            history.push("/registerOnTyping");
          }
        }
      });
    }
  };

  const handleDesp = (e) => {
    setRules(false);
    setViewDecp(true);
  };
  const handleRules = () => {
    setRules(true);
    setViewDecp(false);
  };
  const styles = {
    visibleBorder: {
      borderBottom: "4px solid red",
    },
    hiddenBorder: {
      border: "none",
    },
  };
  return (
    <>
      <div className="alientyping_container">
        <h1 className="alien_heading" style={{ fontFamily: "sans-serif" }}>
          Alien Typing
        </h1>
        {viewDecp && (
          <>
            <h2 style={{ color: "#dcdde9" }}>Description</h2>
            <p
              style={{
                width: "900px",
                lineHeight: "1.5",
                textAlign: "justify",
              }}
            >
              Typing is the process of writing or inputting text by pressing
              keys on a typewriter, computer keyboard, cell phone, or
              calculator. It can be distinguished from other means of text
              input, such as handwriting and speech recognition. Text can be in
              the form of letters, numbers and other symbols.
            </p>
          </>
        )}
        {rules && (
          <>
            <h2>Rules</h2>
            <div className="main_container">
              <div>
                <BsKeyboard className="icon_keyboard" />
                <p>
                  Three rounds will conducted, and every round will have 1
                  minute for typing
                </p>
              </div>
              <div>
                <BsKeyboard className="icon_keyboard" />
                <p>
                  Number of words per minute, accuracy, number of correct and
                  words will be considered
                </p>
              </div>
              <div>
                <BsKeyboard className="icon_keyboard" />
                <p>
                  The best score out of three will be considered for scoring
                </p>
              </div>
            </div>
          </>
        )}

        <div className="alien_buttonGrps">
          <button
            id="description"
            onClick={() => handleDesp()}
            style={viewDecp ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Description
          </button>
          <button
            onClick={() => handleRules()}
            style={rules ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Rules
          </button>
          <Link to="/leaderboard_alienTyping" target="_blank">
            <button>View Scoreboard</button>
          </Link>
        </div>
        {isRegistered && (
          <>
            <h4>
              Whatsapp link:
              <span>
                <a href="https://chat.whatsapp.com/BWmBBssOi0uDdmwFcq1aTY">
                  https://chat.whatsapp.com/BWmBBssOi0uDdmwFcq1aTY
                </a>
              </span>
            </h4>
            <button>Already Registered</button>
          </>
        )}
        {isRegistered || (
          <button onClick={handleRegister} style={{ cursor: "pointer" }}>
            Register
          </button>
        )}
      </div>
    </>
  );
};

export default AlienTyping;
