import React, { useEffect, useState } from "react";
import Card from "./components/Card";
import { events } from "./components/event";
import Header from "./components/Header";
import Timer from "./components/Timer";
import firebase from "firebase";
import Login from "./components/Login";
import Footer from "./components/Footer";
import Aos from "aos";
import "aos/dist/aos.css";
import { Link, animateScroll as scroll } from "react-scroll"; // import { scrollToTop } from "react-scroll/modules/mixins/animate-scroll";
import MainHeader from "./components/MainHeader";

const App = () => {
  const [loaded, setLoaded] = useState(false);
  const [loggedIn, setLoggedIn] = useState(false);
  useEffect(() => {
    Aos.init();
  });

  useEffect(() => {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        setLoaded(true);
        setLoggedIn(true);
      } else {
        setLoaded(true);
        setLoggedIn(false);
      }
    });
  }, []);
  return (
    <>
      {!loaded && (
        <>
          <h2>Loading</h2>
        </>
      )}
      {!loggedIn && loaded && <Login />}

      {loaded && loggedIn && (
        <>
          <MainHeader />
          <div className="headerMain">
            <Header />
          </div>
          <div className="timerMain">
            <Timer />
          </div>
          <div className="eventMain" id="events">
            {events?.map((event) => (
              <Card
                eventName={event.eventName}
                descp={event.descp}
                toMigrate={event.toMigrate}
                startime={event.startime}
                endtime={event.endTime}
                icon={event.icon}
              />
            ))}
          </div>
          <Footer />
        </>
      )}
    </>
  );
};

export default App;
