import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "../css/alienTyping.css";
import { MdOutlineQuiz } from "react-icons/md";
import "../css/bitbybit.css";
import db from "../firebase";
import { useUser } from "./useUser";
import { useHistory } from "react-router-dom";
import "../css/bitbybit.css";

const TechieWeb = () => {
  const [viewDecp, setViewDecp] = useState(true);
  const [rules, setRules] = useState(false);
  const [roundOne, setRoundOne] = useState(false);
  const [roundTwo, setRoundTwo] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [userEmail, setUserEmail] = useState(null);
  let history = useHistory();

  let user = useUser(true);

  useEffect(() => {
    setUserEmail(user.email);
    userIsRegistered();
  }, [user, userIsRegistered]);
  function userIsRegistered() {
    db.collection("registerOnPixels").onSnapshot((snapshot) => {
      let data = snapshot.docs.map((doc) => doc.data());
      if (data) {
        data.map((user) => {
          if (user.email.includes(userEmail)) {
            setIsRegistered(true);
            return true;
          } else return false;
        });
      }
    });
  }

  const handleRegister = () => {
    if (user) {
      let state = false;
      db.collection("registerOnPixels").onSnapshot((snapshot) => {
        let data = snapshot.docs.map((doc) => doc.data());
        if (data) {
          data.map((user) => {
            if (user.email.includes(userEmail)) {
              state = true;
              setIsRegistered(true);
              return;
            }
          });
          if (!state) {
            history.push("/registerOnPixels");
          }
        }
      });
    }
  };
  const handleDesp = (e) => {
    setRules(false);
    setViewDecp(true);
    setRoundOne(false);
    setRoundTwo(false);
  };
  const handleRules = () => {
    setRules(true);
    setViewDecp(false);
    setRoundOne(false);
    setRoundTwo(false);
  };
  const handleRoundOne = () => {
    setRoundOne(true);
    setRules(false);
    setViewDecp(false);
    setRoundTwo(false);
  };
  const handleRoundTwo = () => {
    setRoundOne(false);
    setRules(false);
    setViewDecp(false);
    setRoundTwo(true);
  };

  const styles = {
    visibleBorder: {
      borderBottom: "4px solid red",
    },
    hiddenBorder: {
      border: "none",
    },
  };
  return (
    <>
      <div className="alientyping_container web_conatiner">
        <h1 className="bitbybit_heading">Widget Pixels</h1>
        {viewDecp && (
          <>
            <h2 style={{ color: "#dcdde9" }}>Description</h2>
            <p
              style={{
                width: "900px",
                lineHeight: "1.5",
                textAlign: "justify",
              }}
            >
              Widget Pixels is the process of planning, conceptualizing,
              arranging content online. Today, designing a website goes beyond
              aesthetics to include the websites overall functionality. Web
              design also includes web apps, mobile apps, and user interface
              design.
            </p>
          </>
        )}
        {rules && (
          <>
            <h2>Rules</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  Two rounds will be conducted and every round have different
                  designing concepts
                </p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  Designing style and contents and layouts will be considered
                </p>
              </div>
            </div>
          </>
        )}

        {roundOne && (
          <>
            <h2>RoundOne</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Preliminary round with 20 MCQs related to web designing.</p>
              </div>
            </div>
          </>
        )}

        {roundTwo && (
          <>
            <h2>Round Two</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Responsive dynamic web page for given topic</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>The best score out of two rounds will be considered</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>The decision of judge panel will be final.</p>
              </div>
            </div>
          </>
        )}

        <div className="alien_buttonGrps">
          <button
            id="description"
            onClick={() => handleDesp()}
            style={viewDecp ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Description
          </button>
          <button
            onClick={() => handleRules()}
            style={rules ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Rules
          </button>
          <button
            onClick={() => handleRoundOne()}
            style={roundOne ? styles.visibleBorder : styles.hiddenBorder}
          >
            Round One
          </button>
          <button
            onClick={() => handleRoundTwo()}
            style={roundTwo ? styles.visibleBorder : styles.hiddenBorder}
          >
            Round Two
          </button>

          <Link to="/leaderboard_pixels" target="_blank">
            <button>View Scoreboard</button>
          </Link>
        </div>
        {isRegistered && (
          <>
            <h4>
              Whatsapp link:
              <span>
                <a
                  target="_blank"
                  href="https://chat.whatsapp.com/Fr1ow6WcBM5FtwIDLZPfdB"
                  style={{ color: "white" }}
                >
                  https://chat.whatsapp.com/Fr1ow6WcBM5FtwIDLZPfdB
                </a>
              </span>
            </h4>
            <button> Registered</button>
          </>
        )}
        {isRegistered || (
          <button onClick={handleRegister} style={{ cursor: "pointer" }}>
            Register
          </button>
        )}
      </div>
    </>
  );
};

export default TechieWeb;
