import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../css/alienTyping.css";
import { HiOutlinePresentationChartLine } from "react-icons/hi";
import "../css/bitbybit.css";
import { useUser } from "./useUser";
import db from "../firebase";
import { useHistory } from "react-router-dom";

const LogoLooposy = () => {
  const [viewDecp, setViewDecp] = useState(true);
  const [rules, setRules] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [userEmail, setUserEmail] = useState(null);
  let history = useHistory();

  let user = useUser(true);

  useEffect(() => {
    setUserEmail(user.email);
    userIsRegistered();
  }, [user, userIsRegistered]);
  function userIsRegistered() {
    db.collection("registerLogoLoopsy").onSnapshot((snapshot) => {
      let data = snapshot.docs.map((doc) => doc.data());
      if (data) {
        data.map((user) => {
          if (user.email.includes(userEmail)) {
            setIsRegistered(true);
            return true;
          } else return false;
        });
      }
    });
  }

  const handleRegister = () => {
    if (user) {
      let state = false;
      db.collection("registerLogoLoopsy").onSnapshot((snapshot) => {
        let data = snapshot.docs.map((doc) => doc.data());
        if (data) {
          data.map((user) => {
            if (user.email.includes(userEmail)) {
              state = true;
              setIsRegistered(true);
              return;
            }
          });
          if (!state) {
            history.push("/registerOnlogoloopsy");
          }
        }
      });
    }
  };

  const handleDesp = (e) => {
    setRules(false);
    setViewDecp(true);
  };
  const handleRules = () => {
    setRules(true);
    setViewDecp(false);
  };

  const styles = {
    visibleBorder: {
      borderBottom: "4px solid red",
    },
    hiddenBorder: {
      border: "none",
    },
  };
  return (
    <>
      <div className="alientyping_container logo_container">
        <h1 className="bitbybit_heading">Logo Loopsy</h1>
        {viewDecp && (
          <>
            <h2 style={{ color: "#dcdde9" }}>Description</h2>
            <p
              style={{
                width: "900px",
                lineHeight: "1.5",
                textAlign: "justify",
              }}
            >
              Logo Loopsy is all about creating the perfect visual brand mark
              for specific topic. A logo usually consists of a symbol or
              brand-mark on the logo type along with the tag line.
            </p>
          </>
        )}
        {rules && (
          <>
            <h2>Rules</h2>
            <div className="main_container">
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>
                  Only Individual Participants can participate in the event.
                </p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Design should be submitted in soft copy.</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Online designing software is not allowed to be use</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Software permitted to use: Photoshop, CorelDraw</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>
                  Usage of Reputed companys logos were Rejected. Modification
                  of those logos are also not allowed
                </p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Tagline must be unique</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>Participant need to explain in brief about their design</p>
              </div>
              <div>
                <HiOutlinePresentationChartLine className="icon_keyboard" />
                <p>
                  Winners will be selected on the basis of meaningful,
                  attractive and innovative logos
                </p>
              </div>
            </div>
          </>
        )}

        <div className="alien_buttonGrps">
          <button
            id="description"
            onClick={() => handleDesp()}
            style={viewDecp ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Description
          </button>
          <button
            onClick={() => handleRules()}
            style={rules ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Rules
          </button>

          <Link to="/logoitem" target="_blank">
            <button>View Works by People</button>
          </Link>
        </div>
        {isRegistered && (
          <>
            <h4>
              Whatsapp link:
              <span>
                <a
                  href="https://chat.whatsapp.com/LX4GRMuUFdwEOFGkw8ZZhL"
                  target="_blank"
                  style={{ textDecoration: "underline" }}
                >
                  &nbsp; https://chat.whatsapp.com/LX4GRMuUFdwEOFGkw8ZZhL
                </a>
              </span>
            </h4>
            <button> Registered</button>
          </>
        )}
        {isRegistered || (
          <button onClick={handleRegister} style={{ cursor: "pointer" }}>
            Register
          </button>
        )}
      </div>
    </>
  );
};

export default LogoLooposy;
