import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { HiOutlinePresentationChartLine } from "react-icons/hi";
import { BsKeyboard } from "react-icons/bs";
import { BiTime, BiCalendarStar } from "react-icons/bi";
import { IoLogoFreebsdDevil } from "react-icons/io";
import { MdOutlineQuiz } from "react-icons/md";
import { GrScorecard } from "react-icons/gr";
import "../css/card.css";
import Aos from "aos";
import "aos/dist/aos.css";

const Card = ({
  eventName,
  descp,
  startime,
  endtime,
  toMigrate,
  icon,
  realName,
}) => {
  useEffect(() => {
    Aos.init();
  });
  const styles = {
    icon: {
      fontSize: "1.8rem",
      color: "red",
    },
    mainIcon: {
      fontSize: "3rem",
      color: "red",
    },
  };

  const IconType = (icontype) => {
    console.log(icontype.icontype);
    switch (icontype.icontype) {
      case "keyboard":
        return (
          <>
            <BsKeyboard className="animate_icons" style={styles.mainIcon} />
          </>
        );
      case "presentation":
        return (
          <>
            <HiOutlinePresentationChartLine
              className="animate_icons"
              style={styles.mainIcon}
            />
          </>
        );

      case "quiz":
        return (
          <>
            <MdOutlineQuiz className="animate_icons" style={styles.mainIcon} />
          </>
        );
      case "logo":
        return (
          <>
            <IoLogoFreebsdDevil
              className="animate_icons"
              style={styles.mainIcon}
            />
          </>
        );

      default:
        return (
          <>
            <h1>Not found</h1>
          </>
        );
    }
  };

  return (
    <>
      <div
        className="event_container"
        data-aos="fade-up"
        data-aos-delay="150"
        data-aos-duration="500"
        data-aos-easing="ease-in-out"
      >
        <div className="event_left">
          <IconType icontype={icon} />
        </div>
        <div className="event_right">
          <div className="event_details">
            <div className="event_innerdetails">
              <BiCalendarStar style={styles.icon} />
              <a>{startime}</a>
            </div>
            <div className="event_innerdetails">
              <BiTime style={styles.icon} />
              <a>9.30 to 4 pm</a>
            </div>
            <div className="event_innerdetails">
              <GrScorecard style={styles.icon} />
              <Link to={toMigrate}>Go to page</Link>
            </div>
          </div>
          <div className="event_info">
            <h3 className="event_name">{eventName}</h3>
            <h3 className="event_originalName">{realName}</h3>
            <p> {descp}</p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Card;
