import React, { useEffect, useState } from "react";
import db, { storage } from "../firebase";
import { useStateValue } from "../StateProvider";
import { WifiLoaderComponent } from "./Loader";
import { useHistory } from "react-router-dom";
import "../css/alterlogoloopsy.css";
import styled from "styled-components";

const AlterLogoLoopsy = () => {
  const [{ adminLogoLoopsy }, dispatch] = useStateValue();
  const [name, setName] = useState("");
  const [rollNo, setRollNo] = useState("");
  const [classStu, setClassStu] = useState("");
  const [url, setUrl] = useState("");
  const [image, setImage] = useState(null);
  const [processing, setProcessing] = useState(true);
  const history = useHistory();
  const [animate, setAnimate] = useState("");
  const [mark, setMark] = useState("");

  useEffect(() => {
    if (!adminLogoLoopsy) {
      history.push("/eventadmin");
    }
  }, []);
  useEffect(() => {
    let percent = (element) => {
      return element ? 25 : 0;
    };
    let value =
      percent(name) + percent(rollNo) + percent(classStu) + percent(url);
    setAnimate(value);
  }, [name, rollNo, classStu, url]);

  const handleImage = (e) => {
    console.log(e.target);
    setImage(e.target.files[0]);
  };

  const handleReset = (e) => {
    e.preventDefault();
    setName("");
    setRollNo("");
    setClassStu("");
    setUrl("");
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const uploadTask = storage.ref(`logo/${image.name}`).put(image);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        setProcessing("processing_initialized");
        console.log("processing");
      },
      (error) => {
        console.log(error);
      },
      () => {
        storage
          .ref("logo")
          .child(image.name)
          .getDownloadURL()
          .then((url) => {
            console.log(url);
            setProcessing("processing_ends");
            setUrl(url);
          });

        console.log(url);

        if (url) {
          db.collection("logo").add({
            name: name,
            rollNumber: rollNo,
            studentClass: classStu,
            url: url,
            mark: mark,
          });
          console.log("data successfully added");
        } else {
          console.log("retry");
        }
      }
    );
  };

  return (
    <>
      {adminLogoLoopsy || <h2>Page not found</h2>}
      {adminLogoLoopsy && (
        <>
          <div className="alterLogo_container">
            {processing === "processing_initialized" && (
              <>
                <div className="loader_component">
                  <WifiLoaderComponent />
                </div>
              </>
            )}
            <AnimateHeading value={animate}>
              Logo Loopsy Admin page
            </AnimateHeading>

            <form
              className="alterlogo_form"
              onSubmit={(e) => handleSubmit(e)}
              style={
                processing === "processing_initialized"
                  ? { opacity: "0.3" }
                  : { opacity: "1" }
              }
            >
              <input
                type="text"
                placeholder="enter the name"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
              <input
                type="text"
                placeholder="enter the roll Number"
                value={rollNo}
                onChange={(e) => setRollNo(e.target.value)}
              />
              <input
                type="text"
                placeholder="enter the class"
                value={classStu}
                onChange={(e) => setClassStu(e.target.value)}
              />

              <div>
                <label>Upload your logo pic</label>
                <input type="file" onChange={handleImage} />
              </div>
              <div>
                <input
                  type="number"
                  value={mark}
                  onChange={(e) => setMark(e.target.value)}
                />
              </div>
              <div className="button_grps" style={{ display: "flex" }}>
                <button
                  type="submit"
                  style={{ color: "green", marginRight: "1rem" }}
                >
                  Add
                </button>
                <button
                  style={{ color: "red" }}
                  onClick={(e) => handleReset(e)}
                >
                  reset
                </button>
              </div>
            </form>

            {url && (
              <img className="student_logoImage" src={url} alt="Loading..." />
            )}
          </div>
        </>
      )}
    </>
  );
};

export default AlterLogoLoopsy;

const AnimateHeading = styled.h3`
  margin-top: 3rem;
  display: inline;
  border-radius: 0.1rem;
  &:after {
    content: "";
    position: absolute;
    display: block;
    transition: all 0.5s ease-in-out;
    height: 5px;
    width: ${(props) => props.value * 2.5 + 2}px;
    border-top: 3px solid red;
  }
`;
