import React, { useEffect, useState } from "react";
import db from "../firebase";
import "../css/alien_scoreboard.css";
const LeaderBoard_alientyping = () => {
  const [scoreboard, setScoreboard] = useState([]);

  useEffect(() => {
    db.collection("addAlienTyping")
      //   .orderBy("wpm")
      .onSnapshot((snapshot) => {
        let score = snapshot.docs.map((doc) => doc.data());
        let scoreV2 = score.sort((a, b) => b.wpm - a.wpm);
        setScoreboard(scoreV2);
      });
  }, []);
  return (
    <>
      <div className="alien_scoreboard-container">
        <h2>LeaderBoard for alien Typing</h2>
        <div className="alien-scoreboard">
          <table
            className="alien-scoreboardTable"
            style={{ border: "1px solid black" }}
          >
            <th>Rank</th>
            <th>Name</th>
            <th>Roll Number</th>
            <th>Class</th>
            <th>WPM</th>

            {scoreboard?.map((score, i) => (
              <tr key={i}>
                <td>{i + 1}</td>
                <td>{score.name}</td>
                <td>{score.rollNumber}</td>
                <td>{score.studentClass}</td>
                <td>{score.wpm}</td>
              </tr>
            ))}
          </table>
        </div>
      </div>
    </>
  );
};

export default LeaderBoard_alientyping;
