import React, { useEffect, useState } from "react";
import db from "../firebase";
import { useStateValue } from "../StateProvider";

import styled from "styled-components";

const AlterAlienTyping = () => {
  const [{ adminAlienTyping }, dispatch] = useStateValue();
  // console.log(adminAlienTyping);
  const [name, setName] = useState("");
  const [rollNo, setRollNo] = useState("");
  const [studentClass, setStudentClass] = useState("");
  const [wpm, setWpm] = useState("");
  const [animate, setAnimate] = useState("0%");

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(name, rollNo, studentClass, wpm);
    db.collection("addAlienTyping").add({
      name: name,
      rollNumber: rollNo,
      studentClass: studentClass,
      wpm: wpm,
    });
  };

  useEffect(() => {
    let percent = (element) => {
      return element ? 25 : 0;
    };
    let value =
      percent(name) + percent(rollNo) + percent(studentClass) + percent(wpm);
    setAnimate(value);
  }, [name, rollNo, studentClass, wpm]);

  const handleReset = (e) => {
    e.preventDefault();
    setName("");
    setRollNo("");
    setStudentClass("");
    setWpm("");
  };

  return (
    <>
      {adminAlienTyping || <h1>Page not found</h1>}
      {adminAlienTyping && (
        <>
          <div className="admin_alienTyping alterLogo_container">
            <AnimateHeading value={animate}>
              Alien Typing Admin Page
            </AnimateHeading>
            <form onSubmit={(e) => handleSubmit(e)} className="alterlogo_form">
              <input
                type="text"
                placeholder="Enter the name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
              />
              <input
                type="text"
                placeholder="Enter the Roll Number"
                value={rollNo}
                onChange={(e) => setRollNo(e.target.value)}
                required
              />

              <input
                type="text"
                placeholder="Enter the Class"
                value={studentClass}
                onChange={(e) => setStudentClass(e.target.value)}
              />

              <input
                type="number"
                max="200"
                placeholder="Enter the speed in WPM"
                onChange={(e) => setWpm(e.target.value)}
                value={wpm}
                required
              />
              <div>
                <button
                  type="submit"
                  style={{ color: "green", marginRight: "2rem" }}
                >
                  Add
                </button>
                <button
                  onClick={(e) => handleReset(e)}
                  style={{ color: "red" }}
                >
                  Reset
                </button>
              </div>
            </form>
          </div>
        </>
      )}
    </>
  );
};

export default AlterAlienTyping;

const AnimateHeading = styled.h3`
  margin-top: 3rem;
  display: inline;
  border-radius: 0.1rem;
  &:after {
    content: "";
    position: absolute;
    display: block;
    transition: all 0.5s ease-in-out;
    height: 5px;
    width: ${(props) => props.value * 2.5 + 2}px;
    border-top: 3px solid red;
  }
`;
