import React, { useEffect, useState } from "react";
import "../css/timer.css";
import Aos from "aos";
import "aos/dist/aos.css";

const Timer = () => {
  const [textDay, setTextDay] = useState("");
  const [textHour, setTextHour] = useState("");
  const [textMinute, setTextMinute] = useState("");
  const [textSecond, setTextSecond] = useState("");
  useEffect(() => {
    Aos.init();
  });

  const timeCount = () => {
    const countDate = new Date("Dec 20 ,2021 00:00:00").getTime();

    const now = new Date().getTime();
    const gap = countDate - now;

    const second = 1000;
    const minute = second * 60;
    const hour = minute * 60;
    const day = hour * 24;

    setTextDay(Math.floor(gap / day));
    setTextHour(Math.floor((gap % day) / hour));
    setTextMinute(Math.floor((gap % hour) / minute));
    setTextSecond(Math.floor((gap % minute) / second));
  };

  useEffect(() => {
    setInterval(timeCount, 1000);
  }, []);

  const LineUp = () => {
    return (
      <div className="lineUp">
        <hr />
      </div>
    );
  };
  return (
    <>
      <div
        className="countdown_container"
        data-aos-offset="300"
        data-aos="fade-down"
        data-aos-delay="100"
        data-aos-duration="1200"
        data-aos-easing="ease-in"
      >
        {textSecond + textDay + textMinute + textHour < 0 && (
          <h2>Event Starts</h2>
        )}

        <div className="countdown">
          <div className="container-day">
            <h3 className="day">{textDay}</h3>
            <h3 className="under_text">Day</h3>
          </div>
          <LineUp />
          <div className="container-hour">
            <h3 className="hour">{textHour}</h3>
            <h3 className="under_text">Hour</h3>
          </div>
          <LineUp />
          <div className="container-minute">
            <h3 className="minute">{textMinute}</h3>
            <h3 className="under_text">Minute</h3>
          </div>
          <LineUp />
          <div className="container-second">
            <h3 className="second">{textSecond}</h3>
            <h3 className="under_text">Second</h3>
          </div>
        </div>
      </div>
    </>
  );
};

export default Timer;
