export const initialState = {
  currentUser: null,
  user: false,
  loginIn: false,
  logOut: false,
  adminAlienTyping: false,
  adminBitbyBit: false,
  adminLogoLoopsy: false,
  adminTechieWeb: false,
};

const reducer = (state, action) => {
  switch (action.type) {
    case "ALIEN_TYPING":
      return {
        ...state,
        adminAlienTyping: true,
      };
    case "LOGO_LOOPSY":
      return {
        ...state,
        adminLogoLoopsy: true,
      };
    case "BITBYBIT":
      return {
        ...state,
        adminBitbyBit: true,
      };
    case "TECHIE_WEB":
      return {
        ...state,
        adminTechieWeb: true,
      };

    case "USER_STATE_CHANGE":
      return { ...state, currentUser: action.currentUser };

    default:
      return state;
  }
};

export default reducer;
