import { Button } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useUser } from "./useUser";
import db from "../firebase";
const RegisterQuiz = () => {
  const [name, setName] = useState("");
  const [stuClass, setStuClass] = useState("");
  const [email, setEmail] = useState("");
  const history = useHistory();
  let user = useUser(true);

  useEffect(() => {
    if (user) {
      setEmail(user.email);
    }
  }, [user]);

  const handleButton = () => {
    db.collection("registerQuiz").add({
      team: name,
      studentClass: stuClass,
      email: email,
    });
    history.push("/quiz");
  };
  return (
    <>
      <div>
        <div>
          <h2>Regsitering on Binary Brains</h2>
        </div>
        <form onSubmit={handleButton} className="alterlogo_form">
          <input
            type="text"
            placeholder="enter your team name"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />

          <input
            type="text"
            placeholder="enter your class"
            value={stuClass}
            onChange={(e) => setStuClass(e.target.value)}
          />
          <Button type="submit">Register on Binary Brains</Button>
        </form>
      </div>
    </>
  );
};

export default RegisterQuiz;
