import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "../css/alienTyping.css";
import { MdOutlineQuiz } from "react-icons/md";
import "../css/bitbybit.css";
import db from "../firebase";
import { useUser } from "./useUser";
import { useHistory } from "react-router-dom";

const Quiz = () => {
  const [viewDecp, setViewDecp] = useState(true);
  const [rules, setRules] = useState(false);
  const [roundOne, setRoundOne] = useState(false);
  const [roundTwo, setRoundTwo] = useState(false);
  const [roundThree, setRoundThree] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [userEmail, setUserEmail] = useState(null);
  let history = useHistory();

  let user = useUser(true);

  useEffect(() => {
    setUserEmail(user.email);
    userIsRegistered();
  }, [user, userIsRegistered]);
  function userIsRegistered() {
    db.collection("registerQuiz").onSnapshot((snapshot) => {
      let data = snapshot.docs.map((doc) => doc.data());
      if (data) {
        data.map((user) => {
          if (user.email.includes(userEmail)) {
            setIsRegistered(true);
            return true;
          } else return false;
        });
      }
    });
  }

  const handleRegister = () => {
    if (user) {
      let state = false;
      db.collection("registerQuiz").onSnapshot((snapshot) => {
        let data = snapshot.docs.map((doc) => doc.data());
        if (data) {
          data.map((user) => {
            if (user.email.includes(userEmail)) {
              state = true;
              setIsRegistered(true);
              return;
            }
          });
          if (!state) {
            history.push("/registerOnQuiz");
          }
        }
      });
    }
  };
  const handleDesp = (e) => {
    setRules(false);
    setViewDecp(true);
    setRoundOne(false);
    setRoundThree(false);
    setRoundTwo(false);
  };
  const handleRules = () => {
    setRules(true);
    setViewDecp(false);
    setRoundOne(false);
    setRoundThree(false);
    setRoundTwo(false);
  };
  const handleRoundOne = () => {
    setRoundOne(true);
    setRules(false);
    setViewDecp(false);
    setRoundThree(false);
    setRoundTwo(false);
  };
  const handleRoundTwo = () => {
    setRoundOne(false);
    setRules(false);
    setViewDecp(false);
    setRoundThree(false);
    setRoundTwo(true);
  };
  const handleRoundThree = () => {
    setRoundOne(false);
    setRules(false);
    setViewDecp(false);
    setRoundThree(true);
    setRoundTwo(false);
  };
  const styles = {
    visibleBorder: {
      borderBottom: "4px solid red",
    },
    hiddenBorder: {
      border: "none",
    },
  };
  return (
    <>
      <div className="alientyping_container title_container">
        <h1 className="bitbybit_heading">Binary Brains</h1>
        {viewDecp && (
          <>
            <h2 style={{ color: "#dcdde9" }}>Description</h2>
            <p
              style={{
                width: "900px",
                lineHeight: "1.5",
                textAlign: "justify",
              }}
            >
              Binary Brains(quiz) is a form of game or mind sport in which
              players attempt to answer questions correctly (Technical).
            </p>
          </>
        )}
        {rules && (
          <>
            <h2>Rules</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Only team entries are permitted</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Two Members per team</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  Usage of Mobiles or Any Electronic devices during test will
                  lead to disqualification of the team
                </p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Decision of the quiz master will be final</p>
              </div>
            </div>
          </>
        )}

        {roundOne && (
          <>
            <h2>RoundOne</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>20 Written Questions</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>1 Point for each Correct Answer</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>No Negative Marking</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Star Questions will be considered in case of tie</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  In Case of tie in finals, the scores of the prelims will be
                  counted
                </p>
              </div>
            </div>
          </>
        )}

        {roundTwo && (
          <>
            <h2>Round Two</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  Only Selected teams are allowed to participate in round two
                </p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Each question carries 10 marks</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  Each team will get 2 questions in clockwise and anticlockwise
                  order
                </p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>
                  Once the question is not answered by a team, other team can
                  answer them partial marks will be given
                </p>
              </div>
            </div>
          </>
        )}

        {roundThree && (
          <>
            <h2>Finals</h2>
            <div className="main_container">
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>One question per team</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>It carries 20 Marks</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>No partial Marks</p>
              </div>
              <div>
                <MdOutlineQuiz className="icon_keyboard" />
                <p>Decision of the quiz master will be final</p>
              </div>
            </div>
          </>
        )}

        <div className="alien_buttonGrps">
          <button
            id="description"
            onClick={() => handleDesp()}
            style={viewDecp ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Description
          </button>
          <button
            onClick={() => handleRules()}
            style={rules ? styles.visibleBorder : styles.hiddenBorder}
          >
            View Rules
          </button>
          <button
            onClick={() => handleRoundOne()}
            style={roundOne ? styles.visibleBorder : styles.hiddenBorder}
          >
            Round One
          </button>
          <button
            onClick={() => handleRoundTwo()}
            style={roundTwo ? styles.visibleBorder : styles.hiddenBorder}
          >
            Round Two
          </button>
          <button
            onClick={() => handleRoundThree()}
            style={roundThree ? styles.visibleBorder : styles.hiddenBorder}
          >
            Round Three
          </button>
          <Link to="/" target="_blank">
            <button>View Scoreboard</button>
          </Link>
        </div>
        {isRegistered && (
          <>
            <h4>
              Whatsapp link:
              <span>
                <a
                  style={{ color: "white" }}
                  href="https://chat.whatsapp.com/BRjGP0wQCuU054viSwumfD"
                >
                  https://chat.whatsapp.com/BRjGP0wQCuU054viSwumfD
                </a>
              </span>{" "}
            </h4>
            <button>Already Registered</button>
          </>
        )}
        {isRegistered || (
          <button onClick={handleRegister} style={{ cursor: "pointer" }}>
            Register
          </button>
        )}
      </div>
    </>
  );
};

export default Quiz;
