import React, { useEffect, useState } from "react";
import firebase from "firebase";
export const useUser = (state) => {
  const [user, setUser] = useState(false);
  const getUser = () => {
    firebase.auth().onAuthStateChanged((user) => {
      if (state) {
        setUser(user);
      }
    });
  };
  useEffect(() => {
    getUser();
  }, []);

  return user;
};
