import React, { useEffect, useState } from "react";
import db from "../firebase";
import "../css/alien_scoreboard.css";
const LeaderBoardLogo = () => {
  const [scoreboard, setScoreboard] = useState([]);

  useEffect(() => {
    db.collection("logo").onSnapshot((snapshot) => {
      let score = snapshot.docs.map((doc) => doc.data());
      let scoreV2 = score.sort((a, b) => b.mark - a.mark);
      setScoreboard(scoreV2);
    });
  }, []);
  return (
    <>
      <div className="alien_scoreboard-container">
        <h2 style={{ paddingTop: "2rem" }}>LeaderBoard for Logo designing</h2>
        <div className="alien-scoreboard">
          <table
            className="alien-scoreboardTable"
            style={{ border: "1px solid black" }}
          >
            <th>Rank</th>
            <th>Name</th>
            <th>Roll Number</th>
            <th>Class</th>
            <th>Mark</th>

            {scoreboard?.map((score, i) => (
              <tr key={i}>
                <td>{i + 1}</td>
                <td>{score.name}</td>
                <td>{score.rollNumber}</td>
                <td>{score.studentClass}</td>
                <td>{score.mark}</td>
                <td>{score.round}</td>
              </tr>
            ))}
          </table>
        </div>
      </div>
    </>
  );
};

export default LeaderBoardLogo;
