import React, { useState, useEffect } from "react";
import db from "../firebase";
import { useStateValue } from "../StateProvider";
import styled from "styled-components";

const AlterBitbyBit = () => {
  const [{ adminBitbyBit }, dispatch] = useStateValue();
  const [name, setName] = useState("");
  const [rollNo, setRollNo] = useState("");
  const [studentClass, setStudentClass] = useState("");
  const [mark, setMark] = useState("");
  const [animate, setAnimate] = useState("0%");

  const handleSubmit = (e) => {
    e.preventDefault();
    db.collection("addBitByBit").add({
      name: name,
      rollNumber: rollNo,
      studentClass: studentClass,
      mark: mark,
    });
  };

  useEffect(() => {
    let percent = (element) => {
      return element ? 25 : 0;
    };
    let value =
      percent(name) + percent(rollNo) + percent(studentClass) + percent(mark);
    setAnimate(value);
  }, [name, rollNo, studentClass, mark]);

  const handleReset = (e) => {
    e.preventDefault();
    setName("");
    setRollNo("");
    setStudentClass("");
    setMark("");
  };

  return (
    <>
      {adminBitbyBit && (
        <>
          <div className="admin_alienTyping alterLogo_container">
            <AnimateHeading value={animate}>BitByBit Admin Page</AnimateHeading>
            <form onSubmit={(e) => handleSubmit(e)} className="alterlogo_form">
              <input
                type="text"
                placeholder="Enter the name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
              />
              <input
                type="text"
                placeholder="Enter the Roll Number"
                value={rollNo}
                onChange={(e) => setRollNo(e.target.value)}
                required
              />

              <input
                type="text"
                placeholder="Enter the Class"
                value={studentClass}
                onChange={(e) => setStudentClass(e.target.value)}
              />

              <input
                type="number"
                max="200"
                placeholder="Enter the Mark"
                onChange={(e) => setMark(e.target.value)}
                value={mark}
                required
              />
              <div>
                <button
                  type="submit"
                  style={{ color: "green", marginRight: "2rem" }}
                >
                  Add
                </button>
                <button
                  onClick={(e) => handleReset(e)}
                  style={{ color: "red" }}
                >
                  Reset
                </button>
              </div>
            </form>
          </div>
        </>
      )}
      {adminBitbyBit || <h1>Page not found</h1>}
    </>
  );
};

export default AlterBitbyBit;

const AnimateHeading = styled.h3`
  margin-top: 3rem;
  display: inline;
  border-radius: 0.1rem;
  &:after {
    content: "";
    position: absolute;
    display: block;
    transition: all 0.5s ease-in-out;
    height: 5px;
    width: ${(props) => props.value * 2.5 + 2}px;
    border-top: 3px solid red;
  }
`;
