import React, { useEffect } from "react";
import { useState } from "react";
import db from "../firebase";
import "../css/logoitem.css";
import styled from "styled-components";
const LogoItem = () => {
  const [logodetails, setLogoDetails] = useState([]);
  const [selectName, setSelectName] = useState([]);

  useEffect(() => {
    db.collection("logo").onSnapshot((snapshot) => {
      setLogoDetails(snapshot.docs.map((doc) => doc.data()));
    });
  }, []);
  const handleHeader = (rollNo) => {
    console.log(rollNo);
    const detail = logodetails.filter(
      (logodetail) => logodetail.rollNumber === rollNo
    );
    setSelectName(detail);
  };
  const LogoBanner = () => {
    return (
      <>
        <div className="logo_header">
          {selectName && (
            <>
              {selectName.map((detail, i) => (
                <>
                  <div
                    className="overlay_header"
                    style={{
                      height: "47vh",
                      width: "100%",
                      position: "absolute",
                      backgroundImage: `linear-gradient(365deg, rgba(0,0,0,.4) , rgba(0,0,0,.5) ),url(${detail.url})`,
                      backgroundSize: "cover",
                      zIndex: "-35",
                      filter: "blur(8px)",
                    }}
                  />
                  <h1
                    style={{
                      color: "white",
                      padding: "1.5rem .5rem",
                      position: "absolute",
                    }}
                  >
                    Best logo's
                  </h1>

                  <div
                    className="logoHeader_innercontainer"
                    style={{
                      display: "flex",

                      // backgroundBlendMode: "overlay",
                    }}
                  >
                    <div className="">
                      <img
                        src={detail.url}
                        alt="loading"
                        style={{
                          height: "320px",
                          width: "400px",
                          objectFit: "contain",
                        }}
                      />
                    </div>
                    <div style={{ color: "white" }}>
                      <h3>{detail.name}</h3>
                      <h3>{detail.rollNumber}</h3>
                      <h3>{detail.studentClass}</h3>
                    </div>
                  </div>
                </>
              ))}
            </>
          )}
        </div>
      </>
    );
  };

  return (
    <>
      {/* <h1>share your logo here</h1> */}
      <LogoBanner />

      {logodetails && (
        <div className="logoItem_container">
          {logodetails.map((detail, i) => (
            <div
              className="logoItem_innerContainer"
              onClick={() => handleHeader(detail.rollNumber)}
              key={i}
            >
              <h3>Name: {detail.name}</h3>
              <h3>Roll Number: {detail.rollNumber}</h3>
              <h3>Name: {detail.studentClass}</h3>
              <img src={detail.url} alt="image not found" />
            </div>
          ))}
        </div>
      )}
    </>
  );
};

export default LogoItem;

const logoHeadertop = styled.div`
  display: flex;
`;
