import React, { useState, useEffect } from "react";
import db from "../firebase";
import { useHistory } from "react-router-dom";
import { useStateValue } from "../StateProvider";
import "../css/eventadmin.css";

const EventAdmin = () => {
  const [userName, setUserName] = useState("");
  const [passwd, setPasswd] = useState("");
  const [eventsDetails, setEventsDetails] = useState([]);
  const [{ adminAlienTyping }, dispatch] = useStateValue();
  let history = useHistory();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (userName && passwd) {
      eventsDetails.map((detail) => {
        console.log(detail);
        if (userName === detail.username && passwd === detail.passwd) {
          console.log("user granted");
          dispatch({ type: detail.eventName, show: true });
          // navigate(detail.toMigrate);
          history.push(detail.toMigrate);
        } else {
          console.log("user denied");
        }
      });
    }
  };
  useEffect(() => {
    db.collection("admins").onSnapshot((snapshot) => {
      setEventsDetails(snapshot.docs.map((doc) => doc.data()));
      console.log(eventsDetails);
    });
  }, []);
  return (
    <>
      <div className="eventadmin">
        <div className="top" />
        <div className="bottom" />
        <form onSubmit={(e) => handleSubmit(e)} className="center">
          <input
            type="text"
            value={userName}
            placeholder="enter the username"
            onChange={(e) => setUserName(e.target.value)}
          />
          <input
            type="text"
            value={passwd}
            placeholder="enter the password"
            onChange={(e) => setPasswd(e.target.value)}
          />
          <button type="submit">Submit</button>
        </form>
      </div>
    </>
  );
};

export default EventAdmin;
