import { Button } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useUser } from "./useUser";
import db from "../firebase";
const RegisterOnPixels = () => {
  const [name, setName] = useState("");
  const [rollNo, setRollNo] = useState("");
  const [stuClass, setStuClass] = useState("");
  const [email, setEmail] = useState("");
  const history = useHistory();
  let user = useUser(true);

  useEffect(() => {
    if (user) {
      setEmail(user.email);
    }
  }, [user]);

  const handleButton = () => {
    db.collection("registerOnPixels").add({
      name: name,
      rollNo: rollNo,
      studentClass: stuClass,
      email: email,
    });
    history.push("/techieweb");
  };
  return (
    <>
      <div>
        <div>
          <h2>Regsitering on Widget Pixels</h2>
        </div>
        <form className="alterlogo_form" onSubmit={handleButton}>
          <input
            type="text"
            placeholder="enter your name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
          <input
            type="text"
            placeholder="enter your roll Number"
            value={rollNo}
            onChange={(e) => setRollNo(e.target.value)}
            required
          />
          <input
            type="text"
            placeholder="enter your class"
            value={stuClass}
            onChange={(e) => setStuClass(e.target.value)}
            required
          />
          <Button type="submit">Register on Widget Pixels</Button>
        </form>
      </div>
    </>
  );
};

export default RegisterOnPixels;
